import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Form, Input, Button, Card } from "antd";
import { useMutation } from "@apollo/react-hooks";
import gql from "graphql-tag";

import { SIGNUP_URL, HOME_URL } from "./urls";
import "./SigninPage.css";

/*Form layout from antd.design */

const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 16,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};

const SigninPage = (props) => {
  console.log(props);
  const [errs, setErrs] = useState({});

  const [data, setData] = useState({
    username: "",
    password: "",
  });

  const [signinUser, { loading }] = useMutation(SIGNIN, {
    update(proxy, result) {
      console.log(result);
      props.history.push(HOME_URL);
    },
    onError(err) {
      console.log(err.graphQLErrors[0].extensions.exception.errors);
      setErrs(err.graphQLErrors[0].extensions.exception.errors);
    },
    variables: data,
  });

  const onFinish = (values) => {
    console.log("Received values of form: ", values);
    setData({
      username: values.username,
      password: values.password,
    });

    signinUser();
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  return (
    <Card
      style={{
        width: "500px",
        margin: "auto",
        marginTop: "2em",
      }}
    >
      <Form
        {...layout}
        name="basic"
        initialValues={{
          remember: true,
        }}
        noValidate
        className={loading ? "loading" : ""}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item>
          <h1>Sign In</h1>
        </Form.Item>
        <Form.Item
          label="Username"
          name="username"
          rules={[
            {
              required: true,
              message: "Please input your username!",
            },
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="Password"
          name="password"
          rules={[
            {
              required: true,
              message: "Please input your password!",
            },
          ]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item name="signup">
          <Link to={SIGNUP_URL}>New user? Sign up here!</Link>
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Sign In
          </Button>
        </Form.Item>

        <Form.Item>
          {Object.keys(errs).length > 0 ? (
            <div className="error">
              <ul className="list">
                {Object.values(errs).map((value) => (
                  <li key={value}>{value}</li>
                ))}
              </ul>
            </div>
          ) : (
            <div></div>
          )}
        </Form.Item>
      </Form>
    </Card>
  );
};

const SIGNIN = gql`
  mutation signin($username: String!, $password: String!) {
    signIn(username: $username, password: $password) {
      id
      username
      firstname
      lastname
      email
      token
      createdAt
    }
  }
`;

export default SigninPage;
