import React, { useState } from "react";
import { Form, Input, Button } from "antd";
import { useMutation } from "@apollo/react-hooks";
import gql from "graphql-tag";

import "./SigninPage.css";
import { HOME_URL } from "./urls";

/*Form layout from antd.design */
const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 16,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};

const SignupPage = (props) => {
  const [errs, setErrs] = useState({});
  const [data, setData] = useState({
    username: "",
    firstname: "",
    lastname: "",
    email: "",
    password: "",
    confirmPassword: "",
  });

  const [createUser, { loading }] = useMutation(SIGNUP, {
    update(proxy, result) {
      console.log(result);
      props.history.push(HOME_URL);
    },
    onError(err) {
      console.log(err.graphQLErrors[0].extensions.exception.errors);
      setErrs(err.graphQLErrors[0].extensions.exception.errors);
    },
    variables: data,
  });

  const onFinish = (values) => {
    console.log("Received values of form: ", values);
    setData({
      username: values.username,
      firstname: values.firstname,
      lastname: values.lastname,
      email: values.email,
      password: values.password,
      confirmPassword: values.confirmPassword,
    });

    createUser();
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  return (
    <div style={{ width: "500px", margin: "auto", marginTop: "2em" }}>
      <Form
        {...layout}
        name="basic"
        noValidate
        className={loading ? "loading" : ""}
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item>
          <h1>Sign Up</h1>
        </Form.Item>
        <Form.Item
          label="Username"
          name="username"
          rules={[
            {
              required: true,
              message: "Please input your username!",
            },
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="Firstname"
          name="firstname"
          rules={[
            {
              required: true,
              message: "Please input your firstname!",
            },
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="Lastname"
          name="lastname"
          rules={[
            {
              required: true,
              message: "Please input your lastname!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="Email"
          name="email"
          rules={[
            {
              required: true,
              message: "Please input your email!",
              type: "email",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label="Password"
          name="password"
          rules={[
            {
              required: true,
              message: "Please input your password!",
            },
          ]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          label="Confirm Password"
          name="confirmPassword"
          rules={[
            {
              required: true,
              message: "Please re-enter your password!",
            },
          ]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Sign Up
          </Button>
        </Form.Item>

        <Form.Item>
          {Object.keys(errs).length > 0 ? (
            <div className="ui error message">
              <ul className="list">
                {Object.values(errs).map((value) => (
                  <li key={value}>{value}</li>
                ))}
              </ul>
            </div>
          ) : (
            <div></div>
          )}
        </Form.Item>
      </Form>
    </div>
  );
};

const SIGNUP = gql`
  mutation signup(
    $username: String!
    $email: String!
    $firstname: String!
    $lastname: String!
    $password: String!
    $confirmPassword: String!
  ) {
    signUp(
      userData: {
        username: $username
        firstname: $firstname
        lastname: $lastname
        email: $email
        password: $password
        confirmPassword: $confirmPassword
      }
    ) {
      id
      username
      firstname
      lastname
      email
      token
      createdAt
    }
  }
`;
export default SignupPage;
