import React from "react";
import { Link } from "react-router-dom";
import { HOME_URL, SIGNIN_URL } from "./urls";
import { Menu, PageHeader } from "antd";

import "./Navbar.css";

const Navbar = () => (
  <PageHeader
    ghost={false}
    style={{
      height: "100%",
      background: "inherit",
    }}
    // onBack={() => window.history.back()}
    title={
      <Menu theme="dark" mode="horizontal">
        <Menu.Item key="1">
          <Link to={HOME_URL}>Projects</Link>{" "}
        </Menu.Item>
      </Menu>
    }
    extra={[
      // <Button key="1">
      //   <Link to={LOGIN_URL}>Login</Link>
      // </Button>,
      <Menu key="right" theme="dark" mode="horizontal">
        <Menu.Item key="1">
          <Link to={SIGNIN_URL}>Signin</Link>
        </Menu.Item>
      </Menu>,
    ]}
  />
);

export default Navbar;
