import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { HOME_URL, SIGNIN_URL, SIGNUP_URL, PROJECT_URL, TEST_DOC_SYNC } from "./urls";

import Homepage from "./Homepage";
import SigninPage from "./SigninPage";
import SignupPage from "./SignupPage";
import Workspace from "./Project";
import WorkspaceMulti from "./ProjectMulti";

// Simulated authentication obj, maybe this would be retrieved in cookies
export const fakeAuth = {
  signedIn: false,
};

const RequireAuth = ({ children }) => {
  if (!fakeAuth.signedIn) {
    return <Redirect to={SIGNIN_URL} />;
  }
  return children;
};

const _Signin = () => {
  if (fakeAuth.signedIn) {
    return <Redirect to={HOME_URL} />;
  }
  return <SigninPage />;
};

// Removed <RequireAuth> to test if redirection was working properly
// need to add back in after proper authentication
const AppRouter = () => (
  <Switch>
    <Route exact path={SIGNIN_URL} component={SigninPage} />
    <Route exact path={SIGNUP_URL} component={SignupPage} />

    <Route exact path={HOME_URL} component={Homepage} />
    <Route exact path={PROJECT_URL} component={Workspace} />

    {/* Test */}
    <Route exact path={TEST_DOC_SYNC} component={WorkspaceMulti} />
  </Switch>
);

export default AppRouter;
