import React, { useState, useEffect } from "react";
import { Card, Button, Form, Input } from "antd";
import { Link } from "react-router-dom";
import { PROJECT_URL, TEST_DOC_SYNC } from "./urls";

const Homepage = () => {
  const [form] = Form.useForm();
  const [, forceUpdate] = useState({});

  // To disable submit button at the beginning.
  useEffect(() => {
    forceUpdate({});
  }, []);

  const onFinish = (values) => {
    console.log("Finish:", values);
  };

  return (
    <Card style={{ width: "90%", margin: "auto", marginTop: "2em" }}>
      <h1>Projects</h1>
      <Form
        form={form}
        name="horizontal_login"
        layout="inline"
        onFinish={onFinish}
      >
        <Form.Item
          name="Project"
          rules={[
            { required: true, message: "Please enter the project name!" },
          ]}
        >
          <Input placeholder="Project Name" />
        </Form.Item>
        <Form.Item shouldUpdate>
          {() => (
            <Button
              type="primary"
              htmlType="submit"
              disabled={
                !form.isFieldsTouched(true) ||
                !!form.getFieldsError().filter(({ errors }) => errors.length)
                  .length
              }
            >
              Add
            </Button>
          )}
        </Form.Item>
      </Form>
      {/*  */}
      <br></br>
      <p>TODO: Make List </p>
      Hard coded examples:{" "}
      <ul>
        <li><Link to={PROJECT_URL + "?projectID=ExampleID"}>Example_Project (Solo markdown editor version)</Link></li>
        <li><Link to={TEST_DOC_SYNC + "?projectID=ExampleID"}>Test Multi-user Editing on Editor</Link></li>
      </ul>
    </Card>
  );
};

export default Homepage;
