import React, { useEffect, useState, useRef } from 'react';
import { Card, Row, Col } from 'antd';
import { Collapse } from 'antd';
// import "./markdown-example.css"

import showdown from 'showdown';

/**
 * Useful Docs 
 * Code Mirror Editor   https://uiwjs.github.io/react-codemirror/
 */

import CodeMirror from '@uiw/react-codemirror';
import 'codemirror/keymap/sublime';
import 'codemirror/theme/monokai.css';
import 'codemirror/mode/markdown/markdown';

import Peerify from "./Peerify";

const { Panel } = Collapse;

function safeToString(x) {
    switch (typeof x) {
      case 'object':
        return 'object';
      case 'function':
        return 'function';
      default:
        return x + '';
    }
  }

// Singleton
let savedEditor; 

const WorkspaceMulti = () => {
    
    const [docText, setdocText] = useState("# Heading\n\n Hello");
    const [renderedHTML, setrenderedHTML] = useState("");
    
    let converter = new showdown.Converter();
    let renderHTML = (text) => setrenderedHTML(converter.makeHtml(text));
    

    // Connection
    const otherPeerIdInput        = useRef(null);          // Input for getting another peer id to connect with

    const {
        myPeerId,
        peerMeee,
        peerConns,
        peersData,
        sendPeersData,
        directPeerConnect,
    } = Peerify({
        "onRecieveData": (id, data) => {
            let edit = JSON.parse(data);
            if (savedEditor !== null && savedEditor.doc !== undefined) {
                savedEditor.doc.replaceRange(
                    (edit.origin === "+delete") ? "": edit.text, 
                    edit.from, edit.to, "PEER")
            }
        }
    });

    const handleFormSubmit = () => directPeerConnect(otherPeerIdInput.current.value);

    let ListPeers = () => {
        return Array.from(peersData, ([key, value]) => 
          <div key={key} id={key}>
            { (key in peerMeee.connections) ?
                <h4>Direct Peer: {key}</h4>
              : <h4>Indirect Peer: {key}</h4>
            }
            <pre>{safeToString(value)}</pre>
          </div>
        );
    };

    function ConnectionSetup() {
        return (
            <Collapse defaultActiveKey={['1']}>
            <Panel header="Connection Settings" key="1">
              <h3>My peerId</h3>
              <div>{myPeerId}</div>
              <h3>Join</h3>
              <div>
                <input ref={otherPeerIdInput} type="text" placeholder="Peer" />
                <button type="submit" onClick={handleFormSubmit}>Join</button>
              </div>
              <h3>My Message</h3>
              <textarea onChange={(e) => sendPeersData(e.target.value)}></textarea>
              <h3>Connected Users:</h3>
              <ListPeers/>
              <div>
                <button type="submit" onClick={() => console.log(peerMeee)}>peerMeee</button>
                <button type="submit" onClick={() => console.log(peerConns)}>peerConns</button>
                {/* <button type="submit" onClick={() => console.log()}></button> */}
              </div>
            </Panel>
          </Collapse>
        );
    }
    
    return (
        <>
            <Card style={{ width: "90%", margin: "auto", marginTop: "2em"}}>
                <ConnectionSetup/>
                <br/>
                <h1>Workspace TODO</h1>
                <Row gutter={16}>
                    <Col span={12} style={{display: "grid"}}>
                        <h2>Editor</h2>
                        <CodeMirror
                            value={docText}
                            height="60vh"
                            options={{
                                mode: 'text/x-markdown',
                                theme: 'default',
                                keyMap: 'sublime'
                            }}
                            onChange={(editor, change) => {
                                // console.log(change) 
                                
                                // Singleton
                                if (savedEditor === undefined) {
                                    savedEditor = editor;
                                }
                                
                                if (change.origin !== "PEER") {
                                    sendPeersData(JSON.stringify(change));
                                }

                                var v = editor.getValue("\n");
                                setdocText(v);
                                renderHTML(v);
                            }}
                        />
                    </Col>
                    <Col span={12}>
                        <h2>Renderer</h2> 
                        <div dangerouslySetInnerHTML={{ __html: renderedHTML }} />
                    </Col>
                </Row>
                <br></br>
            </Card>      
            {/* Debug */}  
            <Card style={{ width: "90%", margin: "auto", marginTop: "2em"}}>
                <h1>Set Connection</h1>
                <h1>DEBUG</h1>
                <p>Note, also have to fix repaint issue</p>
                <pre><code>
                    {docText}
                </code></pre>
                <button onClick={()=> console.log(savedEditor)}>Print Editor</button>
            </Card>  
        </>
    )
};

export default WorkspaceMulti;

